import Component from 'flarum/common/Component';
import app from 'flarum/admin/app';
export default class CopyrightSection extends Component {
  view() {
    const {
      copyrightText,
      onUpdate
    } = this.attrs;
    return m("div", {
      className: "FooterConfig-section FooterConfig-copyright"
    }, m("h3", null, app.translator.trans('core.admin.edit_footer.copyright')), m("div", {
      className: "Form-group"
    }, m("label", null, app.translator.trans('core.admin.edit_footer.copyright_text')), m("textarea", {
      className: "FormControl",
      rows: "4",
      value: copyrightText,
      placeholder: app.translator.trans('core.admin.edit_footer.copyright_placeholder'),
      onchange: e => {
        const target = e.target;
        onUpdate(target.value);
      }
    })), m("div", {
      className: "Form-group"
    }, m("small", {
      className: "help-block"
    }, app.translator.trans('core.admin.edit_footer.copyright_help'))));
  }
}
flarum.reg.add('wusong8899-custom-footer', 'admin/components/footer-config/CopyrightSection', CopyrightSection);