import Component from 'flarum/common/Component';
import app from 'flarum/admin/app';
import ListManager from '../common/ListManager';
import LinkEditor from '../common/LinkEditor';
export default class SupportLinksSection extends Component {
  view() {
    const {
      links,
      onUpdate
    } = this.attrs;
    return m("div", {
      className: "FooterConfig-section FooterConfig-supportLinks"
    }, m(ListManager, {
      items: links,
      onUpdate: onUpdate,
      createItem: () => ({
        title: '',
        url: ''
      }),
      renderItem: (link, index, updateItem, removeItem) => m(LinkEditor, {
        link: link,
        onUpdate: updateItem,
        onRemove: removeItem,
        titleLabel: app.translator.trans('core.admin.edit_footer.support_link_title'),
        urlLabel: app.translator.trans('core.admin.edit_footer.support_link_url')
      }),
      title: app.translator.trans('core.admin.edit_footer.support_links'),
      addButtonText: app.translator.trans('core.admin.edit_footer.add_support_link'),
      allowReorder: true
    }), m("div", {
      className: "Form-group"
    }, m("small", {
      className: "help-block"
    }, app.translator.trans('core.admin.edit_footer.support_links_help'))));
  }
}
flarum.reg.add('wusong8899-custom-footer', 'admin/components/footer-config/SupportLinksSection', SupportLinksSection);